
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {Decimal} from 'decimal.js'
import {exchangeItem, dealList} from '@/modules/exchange/api/get_exchange'
import {showloadings, hideloadings} from '@/utils/load'

export default {
name: 'User',
data() {
return {
loadingNum: 8,
uuid: null,
exchangeListData: null,
dealListData: null
}
},
created() {
this.ready()
},
beforeRouteEnter(to, from, next) {



next(vm => {

vm.ready()
})
},
methods: {
ready() {
this.uuid = this.$route.query.uuid

this.getExchangeList(this.uuid)
this.getDealList(this.uuid)
},
getExchangeList(id) {
this.loadingNum -= 1
exchangeItem(id).then(res => {
if (res.status === 200) {
const data = res.data
if (data.code === '200') {
this.exchangeListData = data.data
this.loadingNum += 1
}
}
})
},
getDealList(id) {
this.loadingNum -= 1
dealList(id).then(res => {
if (res.status === 200) {
if (res.data.code === '200') {
this.loadingNum += 1
const data = res.data
console.log(data)
this.dealListData = data
}
}
})
}
},
filters: {
toUpper(val) {
return val.toUpperCase()
},
strIntercept(val) {
return val.substr(2, 17)
},
numberIntercept(val) {
return new Decimal(val).toFixed(8)
}

},
watch: {
loadingNum() {
if (this.loadingNum === 8) {
hideloadings()
} else {
showloadings()
}
}
},
components: {
Group,
XHeader,
Cell
}
}



